@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Roboto", sans-serif}.content{padding:20px;height:calc(100vh - 400px - 73px)}

.header{background:radial-gradient(circle, #5c27fb 0%, #7047f7 100%);height:80px;display:flex;justify-content:center;align-items:center;font-size:1.2rem}.header .logo{color:#fff;justify-self:start;margin-left:20px;cursor:pointer}.header svg{margin-left:0.5rem;font-size:1.6rem}.header .menu{display:grid;grid-template-columns:repeat(5, auto);grid-gap:10px;list-style:none;text-align:center;width:70vw;justify-content:end;margin-right:2rem}.header .menu a{color:#fff;text-decoration:none;padding:0.5rem 1rem}.header .menu a:hover{background-color:#6d76f7;border-radius:4px;transition:all 0.2s ease-out}

.container{padding:80px 60px;background:radial-gradient(circle, #5c27fb 0%, #7047f7 100%)}@media (max-width: 1000px){.container{padding:70px 30px}}.container .wrapper{display:flex;flex-direction:column;justify-content:center;max-width:1000px;margin:0 auto}.container .column{display:flex;flex-direction:column;text-align:left;margin-left:60px}.container .row{display:grid;grid-template-columns:repeat(auto-fill, minmax(230px, 1fr));grid-gap:20px}@media (max-width: 1000px){.container .row{grid-template-columns:repeat(auto-fill, minmax(200px, 1fr))}}.container .link{color:#fff;margin-bottom:18px;font-size:18px;text-decoration:none;cursor:pointer}.container .link:hover{color:#ff9c00;transition:200ms ease-in}.container .link svg{font-size:18px;margin-right:16px}.container .title{font-size:24px;color:#fff;margin-bottom:40px;font-weight:bold}

.home button{margin:10px;padding:0.5rem 1rem;cursor:pointer}.home button:hover{background-color:#cacdf1;border-radius:4px;transition:all 0.2s ease-in-out}

